import React, { Component } from 'react';
import styled from 'styled-components';
import { Strong, Weak } from '../styles/helper';
import {Col, Row} from 'react-styled-flexboxgrid';
import {FaGithub, FaEnvelopeSquare} from 'react-icons/fa';

const PageTitle = styled.h2`
  font-size: 8vh;
  position: absolute;
  top: 10vh;
  right: 10vh;
`
const PageContent = styled.div`
  width: calc(100vw - 20vh);
  height: 65vh;
  position: absolute;
  left: 10vh;
  bottom: 10vh;
`
const ProfileInfoWrap = styled.div`
  margin-bottom: 24px;
`
const ProfileInfoKey = styled.h4`
  font-size: 20px;
  font-weight: 800;
  margin-bottom: 12px;
  position: relative;
  &:after{
    content:'';
    display: block;
    width: 115px;
    height: 1.5px;
    background-color: coral;
    position: absolute;
    left: 0;
    bottom: -8px;
  }
`
const ProfileInfoValue = styled.p`
  font-size: 14px;
  font-weight: 400;
  color: #888;
`
const ProfileImage = styled.div`
  width: 100%;
  height: 65vh;
  background-image: url("/images/profile-image.JPG");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom center;
`
const ContactInfo = styled.div`
  margin-bottom: 6px;
  & svg{
    font-size: 20px;
    margin-right: 4px;
    vertical-align: middle;
  }
  & span{
    vertical-align: middle;
  }
`

const TechInfoListWrap = styled.div`
  height: 65vh;
  position: relative;
  overflow: auto;
`
const TechInfoWrap = styled.div`
  border: 1px solid #eee;
  border-radius: 4px;
  padding: 14px 20px;
  margin-bottom: 20px;
  margin-right: 10px;
`
const TechInfoKey = styled(ProfileInfoKey)`
  text-indent: 16px;
  &:before{
    content: '';
    display: block;
    box-sizing: border-box;
    width: 12px;
    height: 12px;
    border-radius: 6px;
    border: 3px solid coral;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
  }
  &:after{
    display: none;
  }
`
const TechInfoValue = styled(ProfileInfoValue)`
  margin-bottom: 5px;
`
